import { Component, OnInit } from '@angular/core';
import { FormsModule, FormControl, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { User } from '../utils/user';
import { SigninserviceService } from '../services/signinservice/signinservice.service';


@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  signInFormGp: FormGroup;

  // userEmail = new FormControl('');
  // password = new FormControl('');
  loginapi: string = 'On login action a call to authorization server is made! This call is proxied to auth server by Zuul gateway using the authorization sever service id but not the host name itself! Therefore the angular client or Zull gateway doesnt need info about host address and also the port on which the server is running';

  constructor(private user: User, private fb: FormBuilder, private siginService: SigninserviceService) {

  }

  ngOnInit() {
    var that = this;

    that.signInFormGp = that.fb.group({
      email: ['', [Validators.required, Validators.email]],
      password: ['', [Validators.required]],
      authentication:['']
    });

    that.user = { email: '', password: '' };
    that.signInFormGp.valueChanges.subscribe(console.log);
  }

  login(): void {
    var that = this;
    that.signInFormGp.get('authentication').setValue('');
    
    this.siginService.signIn(that.signInFormGp.get('email').value, that.signInFormGp.get('password').value)
      .subscribe(res => {        
        if(res.access_token==undefined){
          console.log("Error sir");
          that.signInFormGp.get('authentication').setValue('email and/or password mismatch');
        }
      })
  }
  get email() {
    return this.signInFormGp.get('email');
  }
  get password() {
    return this.signInFormGp.get('password');
  }

  get authentication() {
    return this.signInFormGp.get('authentication');
  }

}
