import { Component, OnInit } from '@angular/core';
import { FormsModule, FormControl, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { AdditionserviceService } from '../services/additionservice/additionservice.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  info: string = "Now that we are logged in, everytime a new request is sent to server the oauth/bearer token is added to headers";
  additionForm: FormGroup;


  constructor(private fb: FormBuilder, private addService: AdditionserviceService) { }

  ngOnInit() {
    this.additionForm = this.fb.group({
      number1: [0, [Validators.required]],
      number2: [0, [Validators.required]],
      sum: [0]
    });

    this.additionForm.valueChanges.subscribe(console.log);
  }

  get number1() {
    return this.additionForm.get('number1');
  }

  get number2() {
    return this.additionForm.get('number2');
  }

  get sum() {
    return this.additionForm.get('sum');
  }

  add() {
    var that=this;
    that.addService.add(that.additionForm.get('number1').value, that.additionForm.get('number2').value)
                  .subscribe(res => {
                    console.log(res);
                    var outputVal:number = res;
                    that.additionForm.get('sum').setValue(outputVal);
                    });
  }

}
