import { Injectable } from '@angular/core';
import { HttpInterceptor, HttpRequest, HttpHandler, HttpEvent } from '@angular/common/http';
import { Observable } from 'rxjs';
import { SigninserviceService } from '../signinservice/signinservice.service';

@Injectable({
  providedIn: 'root'
})
export class AuthInterceptorService implements HttpInterceptor {

  constructor(private signInService:SigninserviceService) { }

  intercept(req: HttpRequest<any>,
    next: HttpHandler): Observable<HttpEvent<any>> {
    console.log('Intercepted a request ');
    console.log(req.url.toString().includes('uaa/oauth/token'));  
    const idToken = localStorage.getItem("id_token");

    if (idToken && this.signInService.isLoggedIn()) {
      console.log('Id found attaching to request');
      const cloned = req.clone({
        headers: req.headers.set("Authorization",
          "Bearer " + idToken)
      });

      return next.handle(cloned);
    }
    else {
      if(req.url.toString().includes('uaa/oauth/token')){
        const cloned = req.clone({
          headers: req.headers.set("Authorization",
          'Basic ' + btoa('ui:secret'))
        });
  
        return next.handle(cloned);
      }else{
        return next.handle(req);
      }
      
    }
  }

}
