import { Injectable } from '@angular/core';
import { HttpClientModule, HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Router } from '@angular/router';
import { Observable, of } from 'rxjs';
import { tap, map, catchError } from 'rxjs/operators';
import * as moment from 'moment';

@Injectable({
  providedIn: 'root'
})
export class SigninserviceService {


  constructor(private http: HttpClient, private router: Router) { }

  public signIn(username: string, password: string): Observable<any> {
    var loginUrl: string = '/uaa/oauth/token?username=' + username + '&password=' + password + '&grant_type=password';
    var headers: HttpHeaders = new HttpHeaders();


    console.log(username + ' ' + password);

    headers.append('Authorization', 'Basic ' + btoa('ui:secret'));
    headers.append('Content-type', 'application/x-www-form-urlencoded; charset=utf-8');

    return this.http.post<any>(loginUrl, {}, { headers }).pipe(tap(res => {
      this.setSession(res);
    }), catchError(this.handleError('handle', [])));
  }

  private setSession(authResult) {

    const expiresAt = moment().add(authResult.expires_in, 'second');

    localStorage.setItem('id_token', authResult.access_token);
    localStorage.setItem("expires_at", JSON.stringify(expiresAt.valueOf()));

    this.http.get('/uaa/auth/me').subscribe(res=>console.log(res));
    this.router.navigateByUrl('/home');
  }

  private handleError<T>(operation = 'operation', result?: T) {
    return (error: any): Observable<T> => {

      // TODO: send the error to remote logging infrastructure
      console.log(error); // log to console instead

      // TODO: better job of transforming error for user consumption


      // Let the app keep running by returning an empty result.
      return of(result as T);
    };
  }

  logout() {
    localStorage.removeItem("id_token");
    localStorage.removeItem("expires_at");
  }

  public isLoggedIn() {
    return moment().isBefore(this.getExpiration());
  }

  isLoggedOut() {
    return !this.isLoggedIn();
  }

  getExpiration() {
    const expiration = localStorage.getItem("expires_at");
    const expiresAt = JSON.parse(expiration);
    return moment(expiresAt);
  }

}
